import './style.css'
import ReactDOM from 'react-dom'
import { createElement } from 'react'
import { XpraConnection, XpraWindowManager } from './xpra'
import { App } from './ui/App'
import XpraWorker from './xpra/worker?worker'

async function main() {
  const app = document.querySelector<HTMLDivElement>('#app')
  const worker = new XpraWorker()
  const xpra = new XpraConnection(worker)
  const wm = new XpraWindowManager(xpra)

  await xpra.init()

  if (app) {
    ReactDOM.render(createElement(App, { xpra, wm }), app)
  }

  window.xpra = { xpra }
}

window.addEventListener('DOMContentLoaded', () => {
  main()
})
