import { debounce } from 'lodash-es'
import React, {
  useEffect,
  useReducer,
  useState,
  createContext,
  FC,
  Dispatch,
} from 'react'
import {
  XpraConnection,
  XpraWindow,
  XpraWindowIcon,
  XpraConnectionStats,
  XpraWindowMoveResize,
  XpraWindowMetadataUpdate,
  XpraCursor,
  XpraWindowManager,
  XpraNotification,
  XpraWindowInitiateMoveResize,
  XpraConnectionStatus,
  XpraSendFile,
  XpraPointerPosition,
  XpraXDGReducedMenu,
  createNativeNotification,
  browserSaveFile,
  browserPrintFile,
} from '../xpra'
import {
  ActionTypes,
  AppState,
  AppStateActions,
  initialState,
  stateReducer,
} from './store'

export type AppContextProps = {
  xpra: XpraConnection
  wm: XpraWindowManager
}

export const AppContext = createContext<{
  wm: XpraWindowManager
  xpra: XpraConnection
  state: AppState
  root: HTMLElement | null
  dispatch: Dispatch<AppStateActions>
  setRoot: Dispatch<HTMLElement | null>
  setCursor: Dispatch<HTMLElement | null>
}>({
  wm: null!,
  xpra: null!,
  state: initialState,
  root: null,
  setRoot: () => {
    /*placeholder*/
  },
  setCursor: () => {
    /*placeholder*/
  },
  dispatch: () => {
    /*placeholder */
  },
})

const baseParams: string[] = ['host', 'connect']

const booleanParams: string[] = [
  // Custom
  'connect',

  // Internal
  'reconnect',
  'startNewSession',
  'showStartMenu',
  'clipboardImages',
  'clipboard',
  'shareSession',
  'stealSession',
  'fileTransfer',
  'printing',
  'bell',
  'audio',
  'cursor',
  'keyboard',
  'mouse',
  'tray',
  'notifications',
  'ssl',
  'openUrl',
]

const numberParams: string[] = [
  'bandWidthLimit',
  'connectionTimeout',
  'reconnectAttempts',
  'reconnectInterval',
]

function createInitialState() {
  try {
    const { search } = window.location
    const params = new URLSearchParams(search)

    const entries = Array.from(params as unknown as ArrayLike<string>).map(
      ([k, v]) => {
        if (booleanParams.includes(k)) {
          return [k, ['1', 'true', 'on'].includes(String(v).toLowerCase())]
        } else if (numberParams.includes(k)) {
          return [k, parseInt(v)]
        }

        return [k, v ? v : undefined]
      }
    ) as [string, string | number | boolean][]

    const baseEntries = entries
      .filter(([k]) => baseParams.includes(k))
      .filter(([, v]) => v !== undefined)
      .filter(([k]) => initialState.hasOwnProperty(k))

    const optionEntries = entries
      .filter(([k]) => !baseParams.includes(k))
      .filter(([, v]) => v !== undefined)
      .filter(([k]) => initialState.options.hasOwnProperty(k))

    const base = Object.fromEntries(baseEntries)
    const options = Object.fromEntries(optionEntries)

    return {
      ...initialState,
      ...base,
      options: {
        ...initialState.options,
        ...options,
      },
    }
  } catch (e) {
    console.warn('createInitialState', e)
  }

  return initialState
}

const initialCreatedState = createInitialState()

export const AppContextProvider: FC<AppContextProps> = ({
  children,
  wm,
  xpra,
}) => {
  const [state, dispatch] = useReducer(stateReducer, initialCreatedState)

  const [root, setRoot] = useState<HTMLElement | null>(null)
  const [cursor, setCursor] = useState<HTMLElement | null>(null)

  const setConnected = () => {
    dispatch({
      type: ActionTypes.SetConnected,
      payload: true,
    })

    dispatch({
      type: ActionTypes.SetError,
      payload: '',
    })

    Notification.requestPermission()
  }

  const setDisconnected = (s: XpraConnectionStatus) => {
    dispatch({
      type: ActionTypes.SetConnected,
      payload: s !== 'disconnected',
    })
    dispatch({
      type: ActionTypes.ClearWindows,
    })
  }

  const addWindow = (attributes: XpraWindow) => {
    wm.createWindow(attributes)

    dispatch({
      type: ActionTypes.AddWindow,
      payload: attributes,
    })
  }

  const removeWindow = (wid: number) => {
    dispatch({
      type: ActionTypes.RemoveWindow,
      payload: wid,
    })
  }

  const addWindowIcon = (icon: XpraWindowIcon) =>
    dispatch({
      type: ActionTypes.SetWindowIcon,
      payload: icon,
    })

  const updateStats = (stats: XpraConnectionStats) =>
    dispatch({
      type: ActionTypes.UpdateStats,
      payload: stats,
    })

  const updateCursor = (cursor: XpraCursor | null) =>
    dispatch({
      type: ActionTypes.SetCursor,
      payload: cursor,
    })

  const resizeDesktop = (container: HTMLElement) =>
    debounce(() => {
      if (container) {
        const { offsetWidth, offsetHeight } = container
        xpra.sendResize(offsetWidth, offsetHeight)
      }
    }, 200)

  const resizeMoveWindow = (data: XpraWindowMoveResize) => {
    dispatch({
      type: ActionTypes.MoveResizeWindow,
      payload: data,
    })

    if (data.dimension) {
      wm.onResize(data.wid, data.dimension)
    }
  }

  const raiseWindow = (wid: number) =>
    dispatch({
      type: ActionTypes.RaiseWindow,
      payload: wm.getWindow(wid)!.attributes,
    })

  const updateMetadata = (data: XpraWindowMetadataUpdate) =>
    dispatch({
      type: ActionTypes.UpdateMetadata,
      payload: data,
    })

  const initiateMoveResize = (_: XpraWindowInitiateMoveResize) => {
    /* TODO: Implement */
  }

  const keyAction = (wid: number) => (ev: KeyboardEvent) => {
    if (state.connected && ev.target === document.body) {
      ev.preventDefault()
      ev.stopPropagation()
    } else {
      return
    }

    const win = wm.getWindow(wid)
    wm.keyPress(win, ev, ev.type === 'keydown')
  }

  const scrollAction = (wid: number): EventListener =>
    debounce((ev: WheelEvent | MouseEvent | Event) => {
      const win = wm.getWindow(wid)
      if (win) {
        wm.mouseWheel(win, ev)
      }
    }, 100)

  const showNotification = async (notif: XpraNotification) => {
    const nnotif = await createNativeNotification(notif.summary, {
      body: notif.body,
      icon: notif.icon || undefined,
    })

    if (nnotif) {
      const onclose = () => xpra.sendNotificationClose(notif.id)
      nnotif.addEventListener('close', onclose)
    }

    dispatch({
      type: ActionTypes.AddNotification,
      payload: [notif, nnotif],
    })
  }

  const hideNotification = (nid: number) =>
    dispatch({
      type: ActionTypes.RemoveNotification,
      payload: nid,
    })

  const sendFile = (file: XpraSendFile) => {
    if (file.print) {
      browserPrintFile(file.filename, file.blob)
    } else {
      browserSaveFile(file.filename, file.blob)
    }
  }

  const pointerPosition = (pointer: XpraPointerPosition) => {
    if (cursor) {
      const [x, y] = pointer.position
      Object.assign(cursor.style, {
        left: `${x}px`,
        top: `${y}px`,
      })
    }
  }

  const eos = () => {
    /* TODO: Implement */
  }

  const setError = (error: string) =>
    dispatch({
      type: ActionTypes.SetError,
      payload: error,
    })

  const updateMenu = (payload: XpraXDGReducedMenu) =>
    dispatch({
      type: ActionTypes.UpdateMenu,
      payload,
    })

  const sessionStarted = () =>
    dispatch({
      type: ActionTypes.SetStarted,
      payload: true,
    })

  useEffect(() => {
    xpra.on('connect', setConnected)
    xpra.on('disconnect', setDisconnected)
    xpra.on('newWindow', addWindow)
    xpra.on('removeWindow', removeWindow)
    xpra.on('windowIcon', addWindowIcon)
    xpra.on('pong', updateStats)
    xpra.on('cursor', updateCursor)
    xpra.on('moveResizeWindow', resizeMoveWindow)
    xpra.on('updateWindowMetadata', updateMetadata)
    xpra.on('raiseWindow', raiseWindow)
    xpra.on('initiateMoveResize', initiateMoveResize)
    xpra.on('showNotification', showNotification)
    xpra.on('hideNotification', hideNotification)
    xpra.on('sendFile', sendFile)
    xpra.on('pointerPosition', pointerPosition)
    xpra.on('newTray', addWindow)
    xpra.on('eos', eos)
    xpra.on('updateXDGMenu', updateMenu)
    xpra.on('error', setError)
    xpra.on('sessionStarted', sessionStarted)

    if (state.connect) {
      xpra.connect(state.host, state.options)
    }

    return () => {
      xpra.off('connect', setConnected)
      xpra.off('disconnect', setDisconnected)
      xpra.off('newWindow', addWindow)
      xpra.off('removeWindow', removeWindow)
      xpra.off('windowIcon', addWindowIcon)
      xpra.off('pong', updateStats)
      xpra.off('cursor', updateCursor)
      xpra.off('moveResizeWindow', resizeMoveWindow)
      xpra.off('updateWindowMetadata', updateMetadata)
      xpra.off('raiseWindow', raiseWindow)
      xpra.off('initiateMoveResize', initiateMoveResize)
      xpra.off('showNotification', showNotification)
      xpra.off('hideNotification', hideNotification)
      xpra.off('sendFile', sendFile)
      xpra.off('pointerPosition', pointerPosition)
      xpra.off('newTray', addWindow)
      xpra.off('eos', eos)
      xpra.off('updateXDGMenu', updateMenu)
      xpra.off('error', setError)
      xpra.off('sessionStarted', sessionStarted)
    }
  }, [])

  useEffect(() => {
    const resize = resizeDesktop(root!)
    xpra.on('sessionStarted', resize)
    window.addEventListener('resize', resize)

    return () => {
      xpra.off('sessionStarted', resize)
      window.removeEventListener('resize', resize)
    }
  }, [root])

  useEffect(() => {
    const keyCb = keyAction(state.activeWindow)
    const scrollCb = scrollAction(state.activeWindow)

    window.addEventListener('keydown', keyCb)
    window.addEventListener('keyup', keyCb)
    window.addEventListener('wheel', scrollCb)
    window.addEventListener('mousewheel', scrollCb)
    window.addEventListener('DOMMouseScroll', scrollCb)

    return () => {
      window.removeEventListener('keydown', keyCb)
      window.removeEventListener('keyup', keyCb)
      window.removeEventListener('wheel', scrollCb)
      window.removeEventListener('mousewheel', scrollCb)
      window.removeEventListener('DOMMouseScroll', scrollCb)
    }
  }, [state.activeWindow])

  return (
    <AppContext.Provider
      value={{ state, dispatch, root, wm, xpra, setRoot, setCursor }}
    >
      {children}
    </AppContext.Provider>
  )
}
