import React, { useContext } from 'react'
import { AppContext, AppContextProvider } from './context'
import { AppDesktop } from './desktop'
import {
  AppConnectionPanel,
  AppDebugPanel,
  AppTrayPanel,
  AppConnectingPanel,
} from './panels'
import { XpraConnection, XpraWindowManager } from '../xpra'

export function AppInner() {
  const { state } = useContext(AppContext)
  return (
    <>
      {!state.connected && <AppConnectionPanel />}
      {!state.started && state.connected && <AppConnectingPanel />}

      <AppDesktop>{state.connected && <AppDebugPanel />}</AppDesktop>

      {state.started && <AppTrayPanel />}
    </>
  )
}

export function App({
  xpra,
  wm,
}: {
  xpra: XpraConnection
  wm: XpraWindowManager
}) {
  return (
    <AppContextProvider wm={wm} xpra={xpra}>
      <AppInner />
    </AppContextProvider>
  )
}
