import { LANGUAGE_TO_LAYOUT, get_key, get_modifiers } from './lib/keycodes'

interface BrowserFile {
  buffer: Uint8Array
  name: string
  size: number
  type: string
}

const defaultPlatform = ['unknown', ['unknown', 'unknown']]

export const xpraColorGamutMap: Record<string, string> = {
  rec2020: 'rec2020',
  p3: 'P3',
  srgb: 'srgb',
}

export const xpraPlatformMap: Record<string, string[]> = {
  Win: ['win32', 'Microsoft Windows'],
  Mac: ['darwin', 'Mac OSX'],
  Linux: ['linux', 'Linux'],
  X11: ['posix', 'Posix'],
}

export function getBrowserLanguages() {
  const layout = LANGUAGE_TO_LAYOUT as Record<string, string>
  function resolve() {
    const keys = [
      'language',
      'browserLanguage',
      'systemLanguage',
      'userLanguage',
    ]

    const found = keys.find(
      (k) => typeof navigator[k as keyof Navigator] === 'string'
    )

    if (navigator.languages) {
      return navigator.languages
    } else if (found) {
      return [found]
    }

    return ['us']
  }

  return resolve().map((str) => {
    const [shortened] = str.split(/-|_/)
    return layout[str] || layout[shortened] || str
  })
}

export function getBrowser() {
  const [layout] = getBrowserLanguages()
  const ua = navigator.userAgent

  const isSafari =
    !!navigator.vendor.match(/apple/i) &&
    !ua.match(/crios/i) &&
    !ua.match(/fxios/i) &&
    !ua.match(/Opera|OPT\//)

  const browsers = [
    ['MSIE', ua.includes('MSIE') || ua.includes('Trident/')],
    ['Firefox', 'firefox'],
    ['Opera', 'opera'],
    ['Safari', isSafari],
    ['Chrome', 'chrome'],
  ]

  const [name] = browsers.find(([, i]) =>
    typeof i === 'boolean' ? i : ua.toLowerCase().includes(i)
  ) || ['', '']

  return {
    agent: navigator.userAgent,
    name: name as string,
    layout,
  }
}

export function getPlatform() {
  const { appVersion, oscpu, cpuClass } = navigator

  const found = Object.entries(xpraPlatformMap).find(([k]) =>
    appVersion?.includes(k)
  )

  const [, [type, name]] = found || defaultPlatform

  return {
    type,
    name,
    platform: appVersion,
    processor: oscpu || cpuClass || 'unknown',
  }
}

export function getConnectionInfo() {
  const convert = (n?: number | string) =>
    typeof n === 'number' && n > 0 && isFinite(n)
      ? Math.round(n * 1000 * 1000)
      : n

  const result = {
    type: 'unknown',
    downlink: -1,
    downlinkMax: -1,
    effectiveType: '4g',
    rtt: 0,
  }

  if ('connection' in navigator) {
    const append = Object.keys(result)
      .filter((k) => k in navigator.connection)
      .map((k) => [k, navigator.connection[k as keyof NetworkInformation]])

    Object.assign(result, Object.fromEntries(append))
  }

  return {
    ...result,
    downlink: convert(result.downlink),
    downlinkMax: convert(result.downlinkMax),
  } as Record<string, string | number>
}

export const getSupportsClipboard = () =>
  navigator.clipboard &&
  !!navigator.clipboard.readText &&
  !!navigator.clipboard.writeText

export function getColorGamut() {
  if (window.matchMedia) {
    const found = Object.entries(xpraColorGamutMap).find(([k]) =>
      window.matchMedia(`(color-gamut: ${k})`)
    )

    if (found) {
      return found[1]
    }
  }

  return ''
}

export const getDPI = (() => {
  let dpi = 96

  if (typeof window !== 'undefined') {
    if ('deviceXDPI' in screen) {
      dpi = (screen.systemXDPI + screen.systemYDPI) / 2
    } else {
      const el = document.createElement('div') as HTMLDivElement
      el.style.width = '1in'
      el.style.height = '1in'
      el.style.left = '-100%'
      el.style.top = '-100%'
      el.style.position = 'absolute'
      document.body.appendChild(el)
      const { offsetWidth, offsetHeight } = el
      el.remove()

      if (offsetWidth > 0 && offsetHeight > 0) {
        dpi = Math.round((offsetWidth + offsetHeight) / 2.0)
      }
    }
  }

  return () => dpi as number
})()

export function arrayBufferToBase64(uintArray: Uint8Array) {
  // apply in chunks of 10400 to avoid call stack overflow
  // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Function/apply
  let s = ''
  const skip = 10400
  if (uintArray.subarray) {
    for (let i = 0, len = uintArray.length; i < len; i += skip) {
      s += String.fromCharCode.apply(
        null,
        uintArray.subarray(i, Math.min(i + skip, len)) as unknown as number[]
      )
    }
  } else {
    for (let i = 0, len = uintArray.length; i < len; i += skip) {
      s += String.fromCharCode.apply(
        null,
        uintArray.slice(i, Math.min(i + skip, len)) as unknown as number[]
      )
    }
  }
  return window.btoa(s)
}

export function uint8toString(u8a: Uint8Array) {
  const CHUNK_SZ = 0x8000
  const c = []
  for (let i = 0; i < u8a.length; i += CHUNK_SZ) {
    c.push(
      String.fromCharCode.apply(
        null,
        u8a.subarray(i, i + CHUNK_SZ) as unknown as number[]
      )
    )
  }
  return c.join('')
}

export function uint8fromString(input: string) {
  const uint = new Uint8Array(input.length)
  for (let i = 0; i < input.length; ++i) {
    uint[i] = input.charCodeAt(i)
  }
  return uint
}

export function uint8fromStringOrString(input: string | Uint8Array) {
  if (input instanceof Uint8Array) {
    return uint8toString(input)
  }

  return input
}

export function imageSourceFromData(
  encoding: string,
  data: Uint8Array | string
) {
  const imageData = typeof data === 'string' ? uint8fromString(data) : data

  const src = arrayBufferToBase64(imageData)
  const enc = encoding === 'svg' ? 'image/svg+xml' : encoding

  return `data:image/${enc};base64,${src}`
}

export const loadImage = (src: string) =>
  new Promise<HTMLImageElement | null>((resolve) => {
    const image = new Image()
    image.onload = () => {
      if (image.width && image.height) {
        resolve(image)
      } else {
        resolve(null)
      }
    }
    image.onerror = () => {
      resolve(null)
    }
    image.src = src
  })

export function getMouseButton(ev: MouseEvent) {
  let button = 0

  if ('which' in ev)
    // Gecko (Firefox), WebKit (Safari/Chrome) & Opera
    button = Math.max(0, ev.which)
  else if ('button' in ev)
    // IE, Opera (zero based)
    button = Math.max(0, ev.button) + 1

  return button
}

export function getKey(ev: KeyboardEvent | MouseEvent) {
  return get_key(ev)
}

export function getKeyboardModifiers(ev: KeyboardEvent | MouseEvent) {
  return get_modifiers(ev)
}

export function getMousePosition(ev: MouseEvent): [number, number] {
  return [ev.clientX, ev.clientY]
}

export function getMouseScroll(
  event: WheelEvent | MouseEvent | Event
): [number, number] {
  const ev = event as WheelEvent
  const normalize = (num: number) => (num > 1 ? 1 : num < 0 ? -1 : 0)

  if (typeof ev.deltaX === 'number') {
    return [normalize(ev.deltaX), normalize(ev.deltaY)]
  } else if (typeof ev.wheelDelta === 'number') {
    return [0, ev.wheelDelta / 120]
  } else if (typeof ev.wheelDeltaX === 'number') {
    return [ev.wheelDeltaX / 120, ev.wheelDeltaY / 120]
  }

  // TODO: ev.axis
  return [0, 0]
}

export async function createNativeNotification(
  title: string,
  options: NotificationOptions
) {
  if (!('Notification' in window)) {
    throw new Error('Notifications are not supported in this browser')
  }

  if (Notification.permission !== 'denied') {
    const permission = await Notification.requestPermission()
    if (permission === 'granted') {
      const notification = new Notification(title, options)

      return notification
    }
  }

  return null
}

export async function browserLoadFile() {
  const input = document.createElement('input')
  input.type = 'file'

  const load = (blob: Blob) =>
    new Promise<ArrayBuffer>((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.onload = () => {
        resolve(fileReader.result as ArrayBuffer)
      }

      fileReader.onerror = (ev: ProgressEvent) => {
        reject(ev)
      }

      fileReader.readAsArrayBuffer(blob)
    })

  return new Promise<BrowserFile[]>((resolve, reject) => {
    input.onchange = async () => {
      try {
        if (input.files) {
          const promises = Array.from(input.files).map((file) =>
            load(file).then(
              (buffer) =>
                ({
                  buffer,
                  name: file.name,
                  size: file.size,
                  type: file.type,
                } as BrowserFile)
            )
          )

          const result = await Promise.all(promises)
          resolve(result)
        } else {
          resolve([])
        }
      } catch (e) {
        reject(e)
      }
    }

    input.click()
  })
}

export function browserSaveFile(name: string, blob: Blob | string) {
  if (navigator.msSaveOrOpenBlob) {
    navigator.msSaveOrOpenBlob(blob as Blob, name)
  } else {
    const url = blob instanceof Blob ? window.URL.createObjectURL(blob) : blob

    try {
      const a = document.createElement('a')
      a.href = url
      a.download = name
      a.click()
    } finally {
      window.URL.revokeObjectURL(url)
    }
  }
}

export function browserPrintFile(name: string, blob: Blob) {
  const url = URL.createObjectURL(blob)

  try {
    const win = window.open(url)
    if (!win || win.closed || win.closed !== undefined) {
      browserSaveFile(name, url)
    } else {
      win.print()
    }
  } finally {
    window.URL.revokeObjectURL(url)
  }
}

export async function browserReadClipboard(
  textType: string
): Promise<[string, string]> {
  if (navigator.clipboard.read) {
    const read = (blob: Blob) =>
      new Promise<string>((resolve, reject) => {
        const fileReader = new FileReader()

        fileReader.onload = () => {
          resolve(fileReader.result as string)
        }

        fileReader.onerror = (ev: ProgressEvent) => {
          reject(ev)
        }

        fileReader.readAsText(blob)
      })

    const data = await navigator.clipboard.read()
    for (let i = 0; i < data.length; i++) {
      const item = data[i]
      for (let j = 0; j < item.types.length; j++) {
        const itemType = item.types[j]
        const blob = await item.getType(itemType)
        const data = await read(blob)

        if (itemType === 'text/plain') {
          return [textType, data]
        } else if (itemType === 'image/png') {
          return [itemType, data]
        }
      }
    }
  }

  const text = await navigator.clipboard.readText()
  return [textType, text]
}

export async function browserWriteClipboard(data: string, binaryType?: string) {
  if (binaryType) {
    if (navigator.clipboard.write) {
      const blob = new Blob([data], { type: binaryType })
      const item = new ClipboardItem({ [binaryType]: blob })
      const items = [item]

      await navigator.clipboard.write(items)

      return true
    }
  } else {
    await navigator.clipboard.writeText(data)
    return true
  }

  return false
}

export function createHexUUID() {
  const s = []
  const hexDigits = '0123456789abcdef'
  for (let i = 0; i < 36; i++) {
    if (i == 8 || i == 13 || i == 18 || i == 23) {
      s[i] = '-'
    } else {
      s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1)
    }
  }
  return s.join('')
}

export function createRandomSecureString(len: number) {
  const crypto = window.crypto || window.mscrypto

  if (!crypto) {
    let s = ''
    while (s.length < len) {
      s += createHexUUID()
    }
    return s.substr(0, len)
  }

  const u = new Uint8Array(len)
  crypto.getRandomValues(u)

  return String.fromCharCode.apply(null, u as unknown as number[])
}

export function xorString(str1: string, str2: string) {
  let result = ''
  if (str1.length !== str2.length) {
    throw new TypeError('strings must be equal length')
  }
  for (let i = 0; i < str1.length; i++) {
    result += String.fromCharCode(str1[i].charCodeAt(0) ^ str2[i].charCodeAt(0))
  }
  return result
}

// FIXME: Find a modern replacement
export const unescapeUri = (s: string) => unescape(encodeURIComponent(s))
