  private processSendFile(
    filename: string,
    mime: string,
    print: boolean,
    size: number,
    data: Uint8Array
  ) {
    if (data.length !== size) {
      console.warn(
        'XpraConnection#processSendFile',
        'Invalid length for',
        filename
      )
      return
    }

    const blob = new Blob([data], mime as BlobPropertyBag)

    this.emit('sendFile', {
      filename,
      mime,
      size,
      blob,
      print,
    })
  }

  private processSoundData(
    codec: XpraAudioCodecType,
    buffer: Uint8Array | null,
    options: XpraAudioOptions,
    metadata: XpraAudioMetadata
  ) {
    try {
      if (!this.options.audio || !this.audio.validate(codec)) {
        return
      }

      if (options['start-of-stream'] === 1) {
        this.audio.start()
      }

      if (buffer && buffer.length > 0) {
        this.audio.addQueue(buffer, metadata)
      }

      if (options['end-of-stream'] === 1) {
        this.audio.stop()
      }
    } catch (e) {
      console.error('XpraConnection#processSoundData', e)
      this.audio.stop()
    }
  }

  private async processClipboardToken(
    selection: XpraClipboardSelection,
    _targets: XpraClipboardTarget[],
    target: XpraClipboardTarget,
    type: XpraClipboardTarget,
    format: number,
    wireEncoding: string,
    wireData: string | Uint8Array
  ) {
    if (this.options.clipboard) {
      if (arguments.length >= 7) {
        this.clipboardBuffer[selection] = [wireData, type, format, wireEncoding]
      }

      const preferedTargets = this.capabilities['clipboard.preferred-targets']
      const valid = target && preferedTargets.includes(target)

      if (valid) {
        let data = wireData
        const isText = type.match(/text|string/i)

        if (isText) {
          if (data instanceof Uint8Array) {
            data = uint8toString(data)
          }

          await browserWriteClipboard(data)
        } else if (
          type === 'image/png' &&
          format == 8 &&
          wireEncoding == 'bytes'
        ) {
          await browserWriteClipboard(wireData as string, type)
        }
      }
    }
  }

  private processSetClipboardEnabled(enabled: boolean | number) {
    // FIXME: Maybe not mutate the options !?
    this.options.clipboard = !!enabled
  }

  private async processClipboardRequest(
    requestId: number,
    selection: XpraClipboardSelection
  ) {
    const resend = () => {
      const buffer = this.clipboardBuffer['CLIPBOARD']
      if (buffer) {
        const [data, type, format, encoding] = buffer
        this.sendClipboard(requestId, selection, data, type, format, encoding)
      }
    }

    if (this.options.clipboard) {
      if (selection != 'CLIPBOARD') {
        this.sendClipboardString(requestId, selection, '')
        return
      }

      try {
        const result = await browserReadClipboard('UTF8_STRING')
        const buffer = this.clipboardBuffer['PRIMARY']

        if (
          buffer &&
          buffer[1] == 8 &&
          buffer[2] == 'bytes' &&
          result == buffer[3]
        ) {
          resend()
        } else if (result) {
          const [type, data] = result
          this.sendClipboardString(requestId, selection, data, type)
        }
      } catch (e) {
        resend()
        console.error('XpraConnection#processClipboardRequest', e)
      }
    }
  }

  private processChallenge(
    serverSalt: string,
    _cipherOutCaps: string,
    digest: string,
    saltDigest: string,
    prompt: string
  ) {
    prompt = (prompt || 'password').replace(/[^a-zA-Z0-9\.,:\+/]/gi, '')
    saltDigest = saltDigest || 'xor'

    try {
      const password = this.options.password
      if (!password) {
        throw new XpraChallengeError('Invalid login')
      }

      if (saltDigest === 'xor') {
        if (digest === 'xor') {
          if (!this.options.ssl && !this.options.encryption) {
            throw new XpraChallengeError(
              'Aborted challenge because xor is not safe in this environment'
            )
          }
        }

        if (serverSalt.length < 16 || serverSalt.length > 256) {
          throw new XpraChallengeError(
            `Invalid challenge xor salt length: ${serverSalt.length}`
          )
        }
      }

      const clientSaltLength = saltDigest === 'xor' ? serverSalt.length : 32
      const clientSalt = createRandomSecureString(clientSaltLength)

      const salt = createXpraChallengeDigest(saltDigest, clientSalt, serverSalt)
      if (salt) {
        const challengeResponse = createXpraChallengeDigest(
          digest,
          password,
          salt
        )
        if (challengeResponse) {
          this.createCapabilities({
            challenge_response: challengeResponse,
            challenge_client_salt: clientSalt,
          })

          this.sendHello()
          return
        }
      }

      throw new XpraChallengeError('Server challenge was never completed')
    } catch (e) {
      this.disconnect(e as Error)
    }
  }

